import React, {useState, useEffect, useContext, useReducer, useRef} from 'react'
import axios from 'axios'
import {Line} from 'react-chartjs-2'
import '../App.css'
import Collapse from 'react-bootstrap/Collapse'
import Button from 'react-bootstrap/Button'
import thumbnails from '../thumbnails.json'



const totalDataObj = {};
const nameArr = [];
const infoObject= {};
const coins= thumbnails.coins;



function useInterval(callback, delay) {
    const savedCallback = useRef();
    useEffect(() => {
      savedCallback.current = callback;
    }, [callback]);
  
    useEffect(() => {
      function tick() {
        savedCallback.current();
      }
      if (delay !== null) {
        let id = setInterval(tick, delay);
        return () => clearInterval(id);
      }
    }, [delay]);
  }

function CoinNews({ news }){
    return (
        <div className = "news-title">
            {<a href = {news.url}>{news.title}</a>}
        </div>
    )
}

function CoinData({ coin, total }){
    const [open, setOpen] = useState(false);
    const [isNegative, setIsNegative] = useState(false);
    const [coinInfo, setCoinInfo] = useState({})
    const [rankUp, setRankUp] = useState(0);
    const [priceData, setPriceData] = useState([])
    const [fetched , setFetched] = useState(false)
     
    const coinPressed = async()=>{        
        console.log('fetched:',fetched)
        if(!fetched){
                try{
                    let coinGecko = await axios(`https://api.coingecko.com/api/v3/coins/${coin.id}`)
                    //let url = `https://newsapi.org/v2/everything?q=${coin.name}&from=2021-05-21&sortBy=popularity&apiKey=4c6223fc03284828828f00dea9df45e0`;
                    //let req2 = await axios(`https://gnews.io/api/v4/search?q=${coin.name}%20coin&lang=en&token=796224b764bfe17f70d506540822b426`)
                    //let altNews = req2.data.articles;
                    //let req = await axios(url);
                    //let news = req.data.articles;
                   // let news = [...altNews, ...firstNews]
                    infoObject[coin.id] = coinGecko;
                    let homepage = infoObject[coin.id].data.links.homepage[0];
                    let reddit = infoObject[coin.id].data.links.subreddit_url;
                    let twitter = infoObject[coin.id].data.links.twitter_screen_name;
                    let description = infoObject[coin.id].data.description.en;
                    let ath = infoObject[coin.id].data.market_data.ath.usd
                    let athDate = infoObject[coin.id].data.market_data.ath_date.usd;
                    let high24 = infoObject[coin.id].data.market_data.high_24h.usd;
                    let low24 = infoObject[coin.id].data.market_data.low_24h.usd;
                    let volume24 = infoObject[coin.id].data.market_data.total_volume.usd;
                    document.getElementById(`description-div-${coin.id}`).innerHTML = description;
                    setFetched(true)
                    setCoinInfo(coinInfo,
                        coinInfo.homepage = homepage,
                        coinInfo.reddit = reddit,
                        coinInfo.twitter = `https://twitter.com/${twitter}`, 
                        coinInfo.description = description, 
                        coinInfo.ath = ath, 
                        coinInfo.athdate = athDate,
                        coinInfo.high24 = high24,
                        coinInfo.low24 = low24,
                        coinInfo.volume24 = volume24.toLocaleString('en-US'),
                        //coinInfo.news = news
                        );
                }catch(e){
                    if(e.request.status === 404){
                        infoObject[coin.id] = 'none'
                    }
                }
            }
        setOpen(!open)     
    }

    useEffect(()=>{
        let symbol = '';
        let data = totalDataObj[coin.id].price;
        setPriceData(data);
        if(coin.changePercent24Hr != null){
        symbol = coin.changePercent24Hr.split('')[0];
        }
        (symbol === '-')?document.getElementById(`${coinId24}`).classList.add('negative'):document.getElementById(`${coinId24}`).classList.add('positive') 
        if(Number(coin.priceUsd) > data[data.length-2]){
            setIsNegative(false)
            document.getElementById(`${coinIdPrice}`).classList.add('positive')
            document.getElementById(`${coinIdPrice}`).classList.remove('negative')
        }else{
            setIsNegative(true)
            document.getElementById(`${coinIdPrice}`).classList.add('negative')
            document.getElementById(`${coinIdPrice}`).classList.remove('positive')
        }
        if(coin.rank < totalDataObj[coin.id].rank){
            console.log(coin.id, ': up');
            document.getElementById(`${coin.id}rank`).classList.add('positive')
            document.getElementById(`${coin.id}rank`).classList.remove('negative')
            document.getElementById(`${coin.id}outer`).classList.add('positive-box')
            document.getElementById(`${coin.id}outer`).classList.remove('negative-box')
            setRankUp(1);
        }else if (coin.rank > totalDataObj[coin.id].rank){
            console.log(coin.id, ': down')
            document.getElementById(`${coin.id}rank`).classList.add('negative')
            document.getElementById(`${coin.id}rank`).classList.remove('positive')
            document.getElementById(`${coin.id}outer`).classList.add('negative-box')
            document.getElementById(`${coin.id}outer`).classList.remove('positive-box')
            setRankUp(-1);
        }else if (coin.rank === totalDataObj[coin.id].rank){
            setRankUp(0)
        }
    },[coin])

    const figureDom = (total, value)=>{
        let percent = ((value/total)*100).toFixed(2);
        return percent
    }
    const coinId24 = `${coin.id}24`;
    const coinIdPrice = `${coin.id}price`

    //{coinInfo.news && coinInfo.news.map((item)=><CoinNews news = {item} />)}
    return (
        <div className = "coin-div-outer">
            <li key ={coin.id} id ={`${coin.id}outer`} className = 'coin-div'>
                        <div
                            type='button'
                            onClick={coinPressed}
                            id = {coin.id}
                            aria-controls={coin.rank}
                            aria-expanded={false}
                            className = "collapse-btn"
                        >
                        <div id = {`${coin.id}rank`}className = "coin-data rank">{coin.rank}{(rankUp === 0) ? <span></span> : (rankUp < 0) ? <span>&#8681;</span> :  <span>&#8679;</span>}</div>
                        <div className = "coin-data coin-name"><img className = "coin-thumbnail" src = {coins[coin.id]} /> {coin.name}</div><span id = {coinIdPrice}className='price'>${Number(coin.priceUsd).toLocaleString('en-US')}{isNegative ? <span>&#8681;</span>:<span>&#8679;</span> }</span>
                        <div className = "coin-data marketcap">${Number(coin.marketCapUsd).toLocaleString('en-US')}</div>
                        <div className = "coin-data dominance">{figureDom(total, Number(coin.marketCapUsd))}%</div>
                        <div className = "coin-data supply">{Number(coin.supply).toLocaleString('en-US')}</div>
                        <div className = "coin-data max-supply">{Number(coin.maxSupply).toLocaleString('en-US')}</div>
                        <div id={coinId24} className = "coin-data change24">{Number(coin.changePercent24Hr).toLocaleString('en-US')}%</div>
                        </div>
                        <div  >
                            <Collapse id ={coin.rank} className="collapse-inside" in={open}>
                                <div id="collapse-inside-div"> 
                                    <div id="coin-news">
                                        <div id="coin-news-title">{coin.name} News</div>
                                        <div className = "news-scrollable">
                                        
                                        </div>
                                    </div>
                                    <div id="coin-info">
                                        <div id="coin-info-title">{coin.name} Info</div>
                                        <div className = "coin-items item1"><a href = {coinInfo.homepage}>Homepage</a></div>
                                        <div className = "coin-items item2"><a href = {coinInfo.reddit}>Reddit</a></div>
                                        <div className = "coin-items item3"><a href = {coinInfo.twitter}>Twitter</a></div>
                                        <div className = "coin-items item4">All Time High of ${coinInfo.ath} on</div>
                                        <div className = "coin-items item5">{coinInfo.athdate}</div>
                                        <div className = "coin-items item6">24hr High: ${coinInfo.high24}</div>
                                        <div className = "coin-items item7">24hr Low: ${coinInfo.low24}</div>
                                        <div className = "coin-items item8">24hr Volume</div>
                                        <div className = "coin-items item9">${coinInfo.volume24}</div>
                                    </div>
                                    <div className='line-graph'>   
                                    <Line 
                                        className = "graph"
                                        data={{
                                            labels: nameArr,
                                            datasets: [
                                                {
                                                    label: coin.name,
                                                    fill: false,
                                                    lineTension: 0.1,
                                                    backgroundColor: 'rgba(75,192,192,1)',
                                                    borderColor: 'rgba(0,0,0,1)',
                                                    borderWidth: 1,
                                                    data: priceData
                                                }
                                            ]
                                        }}
                                        options={{
                                            title:{
                                                display:true,
                                                text: coin.name,
                                                fontSize:20
                                            },
                                            legend:{
                                            display:true,
                                            position:'right'
                                            }   
                                        }}
                                    />    
                                </div>
                                <div className="history-graph">
                                <Line 
                                        className = "graph"
                                        data={{
                                            labels: nameArr,
                                            datasets: [
                                                {
                                                    label: coin.name,
                                                    fill: false,
                                                    lineTension: 0.1,
                                                    backgroundColor: 'rgba(75,192,192,1)',
                                                    borderColor: 'rgba(0,0,0,1)',
                                                    borderWidth: 1,
                                                    data: priceData
                                                }
                                            ]
                                        }}
                                        options={{
                                            title:{
                                                display:true,
                                                text: coin.name,
                                                fontSize:20
                                            },
                                            legend:{
                                            display:true,
                                            position:'right'               
                                            }   
                                        }}
                                    /> 
                                </div> 
                                <div id = {`description-div-${coin.id}`} className = "description-div"><span>{coinInfo.description}</span></div>
                            </div>
                        </Collapse> 
                    </div>
                </li>
        </div>
    )
}
function MapCoins({ coinData, handleSort, sortOrder, total}) {
    const genDataArr = ()=>{
        const dataArr = [];
        const pushData = ()=>{
            coinData.map((item)=>{
                dataArr.splice(item.rank-1,1,[item.id, Number(item.priceUsd)]);
                if(!totalDataObj[item.id]){
                    totalDataObj[item.id] = {rank:Number(item.rank),price:[]};
                }
                });
            dataArr.map((item)=>{
                let len = totalDataObj[item[0]].price.length;
                if(item[1] != totalDataObj[item[0]].price[len-1]){
                totalDataObj[item[0]].price.push(item[1]);
                }
            nameArr.splice(len-1,1,len)
            })
            
            };
            
            pushData();
            
    }
    genDataArr();
      
    return (
        <div>
            <ol id = "coin-list" >
                <div id="coin-placement-div">
                    {coinData.map(coin=> <CoinData key = {coin.rank} coin = {coin} total = {total} />)} 
                </div>      
            </ol>
        </div>
    
    )   
}
function FetchData() {
    let initialOrder = {
        rank:'none',
        coinname: 'none',
        price: 'none',
        totalmarketcap: 'none',
        dominance: 'none',
        supply: 'none',
        maxsupply: 'none',
        change24: 'none'
    }
    const [totalVal, setTotalVal] = useState(0)
    const [coinData, setCoinData] = useState([]);
    const [sortParam, setSortParam] = useState('rank')
    const [sortOrder, setSortOrder] = useState(initialOrder)
    let [timer, setTimer] = useState(100);

    const fetchData = async ()=>{
        let result = await axios(`https://api.coincap.io/v2/assets/`);
        let resArr = [...result.data.data]
        handleSortParam(resArr, sortParam);
        setTimer(15000);
    }
    useEffect(()=>{
        setTotalVal(()=>{
            let total = 0;
            coinData.map(item=>{
                total += Number(item.marketCapUsd)
            })
            return total;
        });         
    },[coinData]);


    useInterval(()=>{
        fetchData();
    },timer);

    const handleSort = (prop)=>{
        let newParam = prop;
            switch(sortOrder[newParam]){
                case 'none':
                    setSortOrder(sortOrder, sortOrder[sortParam] = 'none');
                    document.getElementById(`${sortParam}-title`).classList.remove('bolder')
                    setSortOrder(sortOrder, sortOrder[newParam] = 'down');
                    document.getElementById(`${newParam}-title`).classList.add('bolder')
                    break;
                case 'down':
                    setSortOrder(sortOrder, sortOrder[sortParam] = 'none');
                    document.getElementById(`${sortParam}-title`).classList.remove('bolder')
                    setSortOrder(sortOrder, sortOrder[newParam] = 'up');
                    document.getElementById(`${newParam}-title`).classList.add('bolder')
                    break;
                case 'up':
                    setSortOrder(sortOrder, sortOrder[sortParam] = 'none');
                    document.getElementById(`${sortParam}-title`).classList.remove('bolder')
                    setSortOrder(sortOrder, sortOrder[newParam] = 'none');
                    document.getElementById(`${newParam}-title`).classList.add('bolder')
                    break;
                default:
                    setSortOrder(initialOrder);
        }
        setSortParam (newParam);
        handleSortParam(coinData,newParam)
    }

    const handleSortParam = (array, param)=>{  
        let sortedData = [...array];
        switch(param){
         case 'coinname':
             switch(sortOrder[param]){
                 case 'none':
                 case 'down':
                    sortedData.sort((a, b) => (a.name > b.name) ? 1 : -1);
                    break;
                case 'up':
                    sortedData.sort((a, b) => (b.name > a.name) ? 1 : -1);
                    break;
             }       
            break;
         case 'totalmarketcap': 
         case 'dominance':
         case 'rank':
            switch(sortOrder[param]){
                case 'none':
                case 'down':
                    sortedData.sort((a, b) => (Number(a.rank) > Number(b.rank)) ? 1 : -1);
                    break;  
               case 'up':
                    sortedData.sort((a, b) => (Number(b.rank) > Number(a.rank)) ? 1 : -1);
                    break;  
            }  
           break; 
         case 'price':
            switch(sortOrder[param]){
                case 'none':
                case 'down':
                    sortedData.sort((a, b) => (Number(b.priceUsd) > Number(a.priceUsd)) ? 1 : -1);
                    break;  
               case 'up':
                    sortedData.sort((a, b) => (Number(a.priceUsd) > Number(b.priceUsd)) ? 1 : -1);
                    break; 
            }  
           break; 
         case 'supply':
            switch(sortOrder[param]){
                case 'none':
                case 'down':
                    sortedData.sort((a, b) => (Number(b.supply)) > Number(a.supply) ? 1 : -1); 
                    break;   
               case 'up':
                    sortedData.sort((a, b) => (Number(a.supply)) > Number(b.supply) ? 1 : -1); 
                    break;  
            }  
           break;  
         case 'maxsupply':
            switch(sortOrder[param]){
                case 'none':
                case 'down':
                    sortedData.sort((a, b) => (Number(b.maxSupply) > Number(a.maxSupply)) ? 1 : -1); 
                    break;   
               case 'up':
                    sortedData.sort((a, b) => (Number(a.maxSupply) > Number(b.maxSupply)) ? 1 : -1); 
                    break;   
            }  
           break;      
         case 'change24':
            switch(sortOrder[param]){
                case 'none':
                case 'down':
                    sortedData.sort((a, b) => (Number(b.changePercent24Hr) > Number(a.changePercent24Hr)) ? 1 : -1);
                    break;   
               case 'up':
                    sortedData.sort((a, b) => (Number(a.changePercent24Hr) > Number(b.changePercent24Hr)) ? 1 : -1);
                    break;   
            }  
           break;        
         default:
              console.log('not working');
         }
        setCoinData(sortedData);
        return sortedData;
        }
    
    

    return (
        <div id="crypto-data">
            <div id="list-div">
                <div id="title-div">
                    <h1>Top 100 CryptoCurrencies</h1>
                    <div id = "total-market-cap">Total Market Val: ${totalVal.toLocaleString('en-US')}</div>
                    <div className = "collapse-btn collapse-btn-title">
                        <div id = "rank-title" className = "coin-data coin-data-title rank" type = "button" onClick = {()=>handleSort('rank')}>Rank{(sortOrder.rank === 'none')?<span></span>:(sortOrder.rank === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "coinname-title" className = "coin-data coin-data-title coin-name" type = "button" onClick = {()=>handleSort('coinname')}>Coin Name{(sortOrder.coinname === 'none')?<span></span>:(sortOrder.coinname === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "price-title" className = "coin-data coin-data-title price" type = "button" onClick = {()=>handleSort('price')}>Price{(sortOrder.price === 'none')?<span></span>:(sortOrder.price === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "totalmarketcap-title" className = "coin-data coin-data-title marketcap" type = "button" onClick = {()=>handleSort('totalmarketcap')}>MarketCap{(sortOrder.totalmarketcap === 'none')?<span></span>:(sortOrder.totalmarketcap === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "dominance-title" className = "coin-data coin-data-title dominance" type = "button" onClick = {()=>handleSort('dominance')}>Dominance{(sortOrder.dominance === 'none')?<span></span>:(sortOrder.dominance === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "supply-title" className = "coin-data coin-data-title supply" type = "button" onClick = {()=>handleSort('supply')}>Supply{(sortOrder.supply === 'none')?<span></span>:(sortOrder.supply === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "maxsupply-title" className = "coin-data coin-data-title max-supply" type = "button" onClick = {()=>handleSort('maxsupply')}>Max Supply{(sortOrder.maxsupply === 'none')?<span></span>:(sortOrder.maxsupply === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                        <div id = "change24-title" className = "coin-data coin-data-title change24" type = "button" onClick = {()=>handleSort('change24')}>24hr Change{(sortOrder.change24 === 'none')?<span></span>:(sortOrder.change24 === 'up')?<span> &#8679;</span>:<span> &#8681;</span>}</div>
                    </div>
                </div>
                <div id="coin-data-div">
                    <MapCoins coinData={coinData} handleSort = {handleSort} sortOrder = {sortOrder} total={totalVal} /> 
                </div>  
            </div>
        </div>
    )
}

export default FetchData
